
import '../components/customers/birthday';
import { CLASS_VISIBLE } from "../tools/constants/classes";
// Selectors
const SELECTOR_RESET_PASSWORD_TOGGLE = '[data-reset-password-toggle]';
const SELECTOR_RESET_PASSWORD_HIDE = '[data-reset-password-hide]';
const SELECTOR_RESET_PASSWORD_FORM = '[data-reset-password-form]';
const SELECTOR_LOGIN_FORM = '[data-login-form]';
const SELECTOR_LOGIN_FORM_WARPPER = '[data-login-form-wrapper]';
const SELECTOR_REGISTER_FORM_WRAPPER = '[data-register-form-wrapper]';
const SELECTOR_REGISTER_BUTTON = '[data-register-submit]';
const SELECTOR_PASSWORD_INPUT = '[data-password]';
const SELECTOR_PASSWORD_CONFIRM_INPUT = '[data-password-confirm]';
const SELECTOR_REGISTER_FORM_PASSWORD_ERROR = '[data-register-password-error]';
const TOGGLE_REGISTER = '[data-account-toggle-register]';
const TOGGLE_LOGIN = '[data-account-toggle-login]';

// Classes
const CLASS_PASSWORD_ERROR = 'has-password-error';

/**
 * Shows the reset password form and hides the login form
 * @param e click event
 */
const resetPasswordShow = (e?: Event) => {
  if (e) e.preventDefault();
  const resetPasswordForm = document.querySelector<HTMLElement>(SELECTOR_RESET_PASSWORD_FORM);
  const loginForm = document.querySelector<HTMLElement>(SELECTOR_LOGIN_FORM);

  if (!loginForm || !resetPasswordForm) return;

  loginForm.classList.remove(CLASS_VISIBLE);
  if (!resetPasswordForm.classList.contains(CLASS_VISIBLE)) {
    resetPasswordForm.classList.add(CLASS_VISIBLE);
  }
}

/**
 * Toggles between showing reset password and login form
 * @param e click event
 */
const resetPasswordToggle = (e?: Event) => {
  if (e) e.preventDefault();

  const resetPasswordForm = document.querySelector<HTMLElement>(SELECTOR_RESET_PASSWORD_FORM);
  const loginForm = document.querySelector<HTMLElement>(SELECTOR_LOGIN_FORM);

  if (!loginForm || !resetPasswordForm) return;

  if (resetPasswordForm.classList.contains(CLASS_VISIBLE)) loginForm.classList.add(CLASS_VISIBLE);
  else loginForm.classList.remove(CLASS_VISIBLE);
  resetPasswordForm.classList.toggle(CLASS_VISIBLE);
}

/**
 * Hides password reset and shows login form
 * @param e click event
 */
const resetPasswordHide = (e?: Event) => {
  if (e) e.preventDefault();
  const resetPasswordForm = document.querySelector<HTMLElement>(SELECTOR_RESET_PASSWORD_FORM);
  const loginForm = document.querySelector<HTMLElement>(SELECTOR_LOGIN_FORM);

  if (!loginForm || !resetPasswordForm) return;

  resetPasswordForm.classList.remove(CLASS_VISIBLE);
  loginForm.classList.add(CLASS_VISIBLE);
}

/**
 * Checks if passwords match
 * Removes errors if they do
 * @param e submit event
 */
const onRegisterFormSubmit = (e?: Event) => {
  let password = (document.querySelector(SELECTOR_PASSWORD_INPUT) as HTMLInputElement).value;
  let passwordConfirm = (document.querySelector(SELECTOR_PASSWORD_CONFIRM_INPUT) as HTMLInputElement).value;
  let registerForm = document.querySelector('form[action$="/account"][method="post"]');
  let passwordErrors = document.querySelector(SELECTOR_REGISTER_FORM_PASSWORD_ERROR);

  // Compare passwords
  if (password !== passwordConfirm) {

    // Do not submit the form
    if (e) {
      e.preventDefault();
      e.stopPropagation();
    }

    // Display errors
    if (registerForm && !registerForm.classList.contains(CLASS_PASSWORD_ERROR)) {
      registerForm.classList.add(CLASS_PASSWORD_ERROR);
    }
    if (passwordErrors && !passwordErrors.classList.contains(CLASS_VISIBLE)) {
      passwordErrors.classList.add(CLASS_VISIBLE);
    }

    return;
  }

  // Remove errors
  if (registerForm && registerForm.classList.contains(CLASS_PASSWORD_ERROR)) {
    registerForm.classList.remove(CLASS_PASSWORD_ERROR);
  }
  if (passwordErrors && passwordErrors.classList.contains(CLASS_VISIBLE)) {
    passwordErrors.classList.remove(CLASS_VISIBLE);
  }
}

/**
 * toggles to login form from register form
 * toggles register/login links
 * @param e event to prevent default
 */
const toggleToLogin = (e?: Event) => {
  if (e) e.preventDefault();

  const registerForm = document.querySelector<HTMLElement>(SELECTOR_REGISTER_FORM_WRAPPER);
  const loginForm = document.querySelector<HTMLElement>(SELECTOR_LOGIN_FORM_WARPPER);
  const registerToggle = document.querySelector<HTMLElement>(TOGGLE_REGISTER);
  const loginToggle = document.querySelector<HTMLElement>(TOGGLE_LOGIN);

  if (!registerForm || !loginForm || !registerToggle || !loginToggle) return;

  registerForm.classList.remove(CLASS_VISIBLE);
  loginToggle.classList.remove(CLASS_VISIBLE);
  loginForm.classList.add(CLASS_VISIBLE);
  registerToggle.classList.add(CLASS_VISIBLE);
}

/**
 * toggles to register form from login form
 * toggles register/login links
 * @param e event to prevent default
 */
const toggleToRegister = (e?: Event) => {
  if (e) e.preventDefault();
  const registerForm = document.querySelector<HTMLElement>(SELECTOR_REGISTER_FORM_WRAPPER);
  const loginForm = document.querySelector<HTMLElement>(SELECTOR_LOGIN_FORM_WARPPER);
  const registerToggle = document.querySelector<HTMLElement>(TOGGLE_REGISTER);
  const loginToggle = document.querySelector<HTMLElement>(TOGGLE_LOGIN);

  if (!registerForm || !loginForm || !registerToggle || !loginToggle) return;

  registerForm.classList.add(CLASS_VISIBLE);
  loginToggle.classList.add(CLASS_VISIBLE);
  loginForm.classList.remove(CLASS_VISIBLE);
  registerToggle.classList.remove(CLASS_VISIBLE);
}

// Shows password reset if hash containers 'recover' or 'recovered-password'
// Shoes success message if password is recovered
// Adds event listeneter for register button
const onPageLoad = () => {
  if (location.pathname == '/account/login' &&
    (location.hash == '#recover' || location.hash == '#recovered-password')) {
    resetPasswordShow();


    if (location.hash == '#recovered-password') {
      let resetPasswordFormContainer = document.querySelector<HTMLElement>('[data-reset-password-form-container]');
      let resetPasswordSuccessMessage = document.querySelector<HTMLElement>('[data-reset-password-success-message]');

      if (!resetPasswordFormContainer || !resetPasswordSuccessMessage) return;
      // toggle success message visible
      if (!resetPasswordFormContainer.classList.contains('is-hidden')) {
        resetPasswordFormContainer.classList.add('is-hidden');
      }
      if (resetPasswordSuccessMessage.classList.contains('is-hidden')) {
        resetPasswordSuccessMessage.classList.remove('is-hidden');
      }
    }
  }

  let registerSubmitButton = document.querySelector(SELECTOR_REGISTER_BUTTON);
  if (registerSubmitButton) {
    (registerSubmitButton as HTMLButtonElement).onclick = (e: Event) => onRegisterFormSubmit(e);
  }

}

// Add event listeners
window.addEventListener('load', () => {
  const reset = document.querySelector(SELECTOR_RESET_PASSWORD_TOGGLE);
  const register = document.querySelector(TOGGLE_REGISTER);
  const login = document.querySelector(TOGGLE_LOGIN);
  const hidePassword = document.querySelector(SELECTOR_RESET_PASSWORD_HIDE);

  if (!reset) return;
  reset.addEventListener('click', e => {
    resetPasswordToggle(e);
  });

  if (!register) return;
  register.addEventListener('click', e => {
    toggleToRegister(e);
  });

  if (!login) return;
  login.addEventListener('click', e => {
    toggleToLogin(e);
  });

  if (!hidePassword) return;
  hidePassword.addEventListener('click', e => {
    resetPasswordHide(e);
  });
});

onPageLoad();