
const SELECTOR_REGISTER_FORM_WRAPPER = '[data-register-form-wrapper]';
const SELECTOR_DAY_SELECTOR = '[data-register-select-day]';
const SELECTOR_MONTH_SELECTOR = '[data-register-select-month]';
const SELECTOR_YEAR_SELECTOR = '[data-register-select-year]';
const SELECTOR_DOB_SELECTOR_NOTE = '[data-register-dob-note]';
const SELECTOR_DOB_SELECTOR_TAG = '[data-register-dob-tag]';
const CLASS_SELECTED = 'is-selected';

document.querySelectorAll<HTMLElement>(SELECTOR_REGISTER_FORM_WRAPPER).forEach(container => {

  const daySelect = container.querySelector<HTMLSelectElement>(SELECTOR_DAY_SELECTOR);
  const monthSelect = container.querySelector<HTMLSelectElement>(SELECTOR_MONTH_SELECTOR);
  const yearSelect = container.querySelector<HTMLSelectElement>(SELECTOR_YEAR_SELECTOR);
  const dateInputNote = container.querySelector<HTMLInputElement>(SELECTOR_DOB_SELECTOR_NOTE);
  const dateInputTag = container.querySelector<HTMLInputElement>(SELECTOR_DOB_SELECTOR_TAG);

  let day:string;
  let month:string;
  let year:string;

  if (!daySelect || !monthSelect || !yearSelect) return;

  const onDobChange = () => {
    if (!day || !month || !year || !dateInputNote || !dateInputTag) return;

    if (day.length == 1) day = `0${day}`;
    if (month.length == 1) month = `0${month}`;

    // This is the DOB that will be used to display in Klaviyo and added to customer note
    dateInputNote.value = `${year}-${month}-${day}`;

    // This is the DOB that will be used to display in the account page and added to customer tags
    // The format will be dd/mm/yyyy
    dateInputTag.value += `,Birthday__${day}/${month}/${year}`;
  }

  const onDayChange = (e: Event) => {
    let self = e.currentTarget as HTMLInputElement;
    self.classList.add(CLASS_SELECTED);
    day = self.value;
    onDobChange();
  }

  const onMonthChange = (e: Event) => {
    let self = e.currentTarget as HTMLInputElement;
    self.classList.add(CLASS_SELECTED);
    month = self.value;
    onDobChange();
  }

  const onYearChange = (e: Event) => {
    let self = e.currentTarget as HTMLInputElement;
    self.classList.add(CLASS_SELECTED);
    year = self.value;
    onDobChange();
  }

  daySelect.addEventListener('change', (e) => {onDayChange(e)});
  monthSelect.addEventListener('change', (e) => { onMonthChange(e) });
  yearSelect.addEventListener('change', (e) => { onYearChange(e) });
});


